import * as React from 'react';
import * as ReactDOM from 'react-dom/client';
import App from './App';
import { HashRouter } from 'react-router-dom';
import {
  GoogleReCaptchaProvider,
  GoogleReCaptcha
} from 'react-google-recaptcha-v3';

/**
 * The Root ELement which renders all of the application screens.
 */
const rootElement = document.getElementById('root');
const root = ReactDOM.createRoot(rootElement);

// Function to handle reCaptchana changes
function onRecaptchaChange(value) {
  localStorage.setItem("vct_captcha_token", value);
}

root.render(
    <React.StrictMode>
    {/* Captcha giving token which will be sent to backend */}
    <GoogleReCaptchaProvider reCaptchaKey={process.env.REACT_APP_RECAPTCHA_API_KEY}>
      <GoogleReCaptcha onVerify={onRecaptchaChange} />
    </GoogleReCaptchaProvider>      
    <HashRouter>
      <App />
    </HashRouter>
  </React.StrictMode>
);
